.container {
  background-color: #4d4c7d;
  padding: 40px 30px;
}

.options {
  padding: 20px 150px;
  background-color: #f5f5f5;
  box-shadow: -1px 7px 5px 0px rgba(0, 0, 0, 0.39);
  display: flex;
  gap: 40px;
}
.box_link {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #f5f5f5;
  text-align: center;
  padding: 10px;
  transition-property: transform;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.box_link:hover {
  box-shadow: 0px 7px 12px 0px rgba(0, 0, 0, 0.59);
  transform: scale(1.1);
}

.link_back {
  font-weight: 700;
  color: #363062;
}

.link_back:hover {
  color: #f99417;
}

.link_back:focus {
  color: #f99417;
}

.nav_link {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 25px;
  color: #363062;
}

.nav_link.active {
  color: #f99417;
}

.nav_link:hover {
  color: #ff8f07b2;
}

.nav_link:focus {
  color: #ff8f07b2;
}
